:root {
  --background-color: #252525;
  --primary-text-color: #e7e7e7;
  --secondary-text-color: #777676;
  --contrast-color: #ff5f80;
  --icons-color: #777777;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Source Sans Pro', sans-serif;
}

body {
  font-size: 62.5%;
  background-color: #252525;
  background-color: var(--background-color);
  color :#e7e7e7;
  color :var(--primary-text-color);
}

#root {
  display: flex;
  margin: 0 auto;
  position: relative;
  width: 90%;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
}

/* scrollbar */

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  margin: 20px 0;
  border-radius: 100vw;
}

::-webkit-scrollbar-track {
  background-color: #3f3f3f;

}

::-webkit-scrollbar-thumb {
  background-color: #75696b;
  border: 2.5px solid #3f3f3f;
  border-radius: 100vw;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #948488 ;
}

::-webkit-scrollbar-thumb:active {
  background-color: #c9808e;
}


header {
    display: flex;
    margin-bottom: 30px;
    justify-content: center;
    align-items: center;
}

header .title {
    font-family: 'Caveat', cursive;
    font-size: 3.5rem;
    color: var(--primary-text-color);
}

@media (min-height: 640px) {
    header {
        margin-bottom: 30px;
    }
}

@media (min-width: 800px) {
    header .title {
        font-size: 4rem;
    }
}
.no-tasks-message {
    display: flex;
    margin-bottom: 40px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.no-tasks-message .no-results {
    margin-bottom: 20px;
    height: 110px;
    width: 110px;
    -webkit-animation: float 6s ease-in-out infinite;
            animation: float 6s ease-in-out infinite;
}

@-webkit-keyframes float {
	0% {
        -webkit-filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
                filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
        -webkit-transform: translatey(0px);
                transform: translatey(0px);
    }
	50% {
        -webkit-filter: drop-shadow(0 25px 15px rgba(0,0,0,0.3));
                filter: drop-shadow(0 25px 15px rgba(0,0,0,0.3));
        -webkit-transform: translatey(-15px);
                transform: translatey(-15px);
    }
	100% {
        -webkit-filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
                filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
        -webkit-transform: translatey(0px);
                transform: translatey(0px);
    }
}

@keyframes float {
	0% {
        -webkit-filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
                filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
        -webkit-transform: translatey(0px);
                transform: translatey(0px);
    }
	50% {
        -webkit-filter: drop-shadow(0 25px 15px rgba(0,0,0,0.3));
                filter: drop-shadow(0 25px 15px rgba(0,0,0,0.3));
        -webkit-transform: translatey(-15px);
                transform: translatey(-15px);
    }
	100% {
        -webkit-filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
                filter: drop-shadow(0 5px 15px rgba(0,0,0,0.7));
        -webkit-transform: translatey(0px);
                transform: translatey(0px);
    }
}

.no-tasks-message h3 {
    font-size: 1.3rem;
    color: var(--primary-text-color);
}

.no-tasks-message p {
    font-size: 1rem;
    color: var(--secondary-text-color);
}

.no-tasks-message .arrow {
    display: none;
    margin-top: 35px;
    width: 150px;
    height: 150px;
    opacity: .3;
}

@media (min-height: 640px) {
    .no-tasks-message .arrow {
        display: inline-block;
    }
}
.tasks-counter-container {
  display: flex;
  margin-bottom: 15px;
  flex-direction: column;
  align-items: center;
}

.TodoCounter {
  font-size: 1.2rem;
  color: #a5a5a5;
}

.TodoCounter span {
  font-size: 1.2rem;
  font-weight: 700;
  color: #d8d8d8
}

.TodoSearch-container {
  display: flex;
  margin: 0 auto 10px;
  flex-direction: column;
  align-items: center;
}

.TodoSearch {
  padding-left: 15px;
  width: 90%;
  height: 50px;
  border: none;
  border-radius: 5px;
  outline: none;
  font-size: 1.2rem;
  color: #afafaf;
  background-color: #3f3f3f;
}
  
@media (min-width: 600px) {
  .TodoSearch-container {
    max-width: 540px;
  }
}
.list-container {
  margin: 0 auto 20px;
  height: 120px;
  overflow-y: auto; 
}

@media (min-height: 600px) {
  .list-container {
    height: 250px;
  }
}

@media (min-height: 740px) {
  .list-container {
    height: 330px;
  }
}

@media (min-height: 1024px) {
  .list-container {
    height: 540px;
  }
}

@media (min-width: 600px) {
  .list-container {
    max-width: 540px;
  }
}
.TodoItem {
    display: flex;
    position: relative;
    margin: 20px auto 0;
    padding: 5px;
    width: 90%;
    height: auto;
    border: none;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    background-color: #474243;
}
  
.TodoItem-p {
  margin-left: 10px;
  width: 70%;
  font-size: 1.1rem;
  font-weight: 400;
  word-break: break-word;
}

.TodoItem-p--complete {
  color: var(--icons-color);
  text-decoration: line-through;
}

.Icon-check {
  position: absolute;
  left: 8px;
  color: var(--icons-color);
  cursor: pointer;
}

.TodoItem .check-size {
  width: 30px;
  height: 30px;
}

.Icon-check--active {
  color: #62a164;
}

.Icon-delete {
  position: absolute;
  top: -8px;
  right: 10px;
  color: var(--icons-color);
  cursor: pointer;
  transition: all .3s ease;
}

.TodoItem .cross-size {
  width: 15px;
  height: 15px;
}

.Icon-delete:hover {
  color: var(--contrast-color);
}
form {
    display: flex;
    width: 80%;
    /* padding: 15px; */
    border-radius: 5px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(to bottom, #e9e9e9, #e1e1e1, #dadada, #d2d2d2, #cbcbcb);
    box-shadow: 0 5px 14px #131212;
}

label {
    padding: 5px 10px;
    width: 100%;
    text-align: center;
    font-size: 1.3rem;
    color: var(--background-color);
}

textarea {
    height: 100px;
    width: 85%;
    padding: 8px 10px;
    border: 2px solid var(--background-color);
    border-radius: 5px;
    color: var(--background-color);
    box-shadow: inset 0 0 8px #9e9e9e;
    resize: none;
    outline: none;
    overflow-wrap: break-word;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
    font-size: 1rem;
}

textarea::-webkit-input-placeholder {
    color: #5f5f5f;
}

textarea:-ms-input-placeholder {
    color: #5f5f5f;
}

textarea::placeholder {
    color: #5f5f5f;
}

.buttons-container {
    display: flex;    
    margin: 10px 0;
    width: 85%;
    justify-content: space-between;
}

.buttons-container button {
    padding: 5px;
    width: 110px;
    border: none;
    border-radius: 5px;
    font-size: .9rem;
    letter-spacing: .1rem;
    color: var(--primary-text-color);
    cursor: pointer;
}

button:first-child {
    background-color: var(--contrast-color);
}

button:first-child:hover {
    background-color: #e25672;
}

button:nth-child(2) {
    background-color: #499c4b;
}

button:nth-child(2):hover {
    background-color: #408641;
}

@media (min-width: 400px) {
    .buttons-container {
        justify-content: space-around;
    }
    .buttons-container button {
        width: 130px;
    }
}

@media (min-width: 500px) {
    form {
      height: 250px;
      max-width: 400px;
    }
    label {
        font-size: 1.8rem;
    }
    textarea {
        height: 140px;
        font-size: 1.2rem;
    }
    .buttons-container button {
        font-size: 1.1rem;
        height: 40px;
    }
  }
.modal-bg {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.button-container {
    display: flex;
    margin-bottom: 10px;
    width: 100%;
    justify-content: center;
    /* align-items: center; */
}

.CreateTodoButton {
    /* display: block; */
    margin: 0 auto;
    width: 70px;
    height: 70px;
    border: none;
    border-radius: 50%;
    color: var(--contrast-color);
    transition: all .4s ease;
    cursor: pointer;
}

@-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.CreateTodoButton:hover {
    color: #c73e5a;
    -webkit-animation: spin 2s infinite;
            animation: spin 2s infinite;
    -webkit-animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
            animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    
}

